/* 1. Global - Set up the things
/* ---------------------------------------------------------- */

/* Import CSS reset and base styles */
@import "global.css";

:root {

    /* Wordman Coffee Color Palette */
    --color-coffee-darker: #1A0D00;     /* Deep espresso - darkest */
    --color-coffee-dark: #2D1810;       /* Rich roast - dark backgrounds */
    --color-coffee-medium: #4A3228;     /* Medium roast - borders/accents */
    --color-coffee-light: #C4A882;      /* Creamy latte - body text */
    --color-coffee-cream: #F5E6D3;      /* Light cream - highlights */
    --color-coffee-accent: #E67E22;     /* Modern warm orange - links/accent */
    --color-coffee-caramel: #D4A574;    /* Caramel - secondary accent */
    --color-coffee-foam: #FDF7EF;       /* Steamed milk foam */
    --color-coffee-haze: #F8EDE0;       /* Morning haze */
    --color-coffee-glow: #FFB978;       /* Sunrise glow */
    --color-coffee-mocha: #3B2218;      /* Mocha depth */

    --radius-card: 20px;
    --radius-pill: 999px;
    --shadow-soft: 0 18px 45px rgba(45, 24, 16, 0.12);
    --shadow-card: 0 14px 30px rgba(45, 24, 16, 0.1);
    --shadow-card-hover: 0 22px 50px rgba(45, 24, 16, 0.16);
    --transition-slow: 0.6s cubic-bezier(0.16, 1, 0.3, 1);

    /* Legacy color mappings for compatibility */
    --color-green: #a4d037;
    --color-yellow: #fecd35;
    --color-red: #f05230;
    --color-darkgrey: var(--color-coffee-dark);
    --color-midgrey: var(--color-coffee-light);
    --color-lightgrey: var(--color-coffee-cream);
    --color-secondary-text: var(--color-coffee-light);
    --color-border: var(--color-coffee-medium);
    --color-wash: var(--color-coffee-cream);
    --color-darkmode: var(--color-coffee-darker);

    /*
    An accent color is also set by Ghost itself in
    Ghost Admin > Settings > Brand

    --ghost-accent-color: var(--color-coffee-accent);

    You can use this variable throughout your styles
     */

    /* Fonts - Wordman Brand */
    --font-sans: 'Sora', sans-serif;
    --font-serif: 'Fraunces', 'Playfair Display', Georgia, Times, serif;
    --font-mono: 'JetBrains Mono', Menlo, Courier, monospace;
    --font-heading: 'Fraunces', 'Playfair Display', Georgia, serif;

}


/* 2. Layout - Page building blocks
/* ---------------------------------------------------------- */

.viewport {
    position: relative;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.viewport::before {
    content: "";
    position: fixed;
    inset: 0;
    pointer-events: none;
    background:
        radial-gradient(800px 400px at 8% 15%, rgba(255, 185, 120, 0.14), transparent 65%),
        radial-gradient(900px 480px at 92% 8%, rgba(212, 165, 116, 0.16), transparent 70%),
        repeating-linear-gradient(
            120deg,
            rgba(45, 24, 16, 0.04) 0px,
            rgba(45, 24, 16, 0.04) 1px,
            transparent 1px,
            transparent 8px
        );
    opacity: 0.4;
    z-index: 0;
}

.site-content {
    flex-grow: 1;
    position: relative;
    z-index: 1;
}

/* Full width page blocks */
.outer {
    position: relative;
    padding: 0 max(4vmin, 20px);
}

/* Centered content container blocks */
.inner {
    margin: 0 auto;
    max-width: 1200px;
    width: 100%;
}


/* 3. Site Header
/* ---------------------------------------------------------- */

.site-header {
    position: relative;
    color: var(--color-coffee-cream);
    background:
        radial-gradient(1100px 640px at 18% 12%, rgba(255, 214, 170, 0.18), transparent 62%),
        radial-gradient(900px 540px at 86% 6%, rgba(212, 165, 116, 0.22), transparent 65%),
        linear-gradient(140deg, #24150f 0%, #3a2418 46%, #8a5a3c 100%);
    box-shadow: inset 0 -1px 0 rgba(255, 255, 255, 0.08);
    overflow: hidden;
}

.site-header::before {
    content: "";
    position: absolute;
    inset: 0;
    background:
        radial-gradient(720px 360px at 18% 20%, rgba(255, 255, 255, 0.08), transparent 60%),
        radial-gradient(680px 380px at 82% 12%, rgba(255, 255, 255, 0.06), transparent 60%);
    pointer-events: none;
    z-index: 0;
}

.site-header::after {
    content: "";
    position: absolute;
    inset: -10% 0 0 0;
    background:
        radial-gradient(240px 120px at 20% 30%, rgba(255, 255, 255, 0.16), transparent 70%),
        radial-gradient(300px 140px at 80% 20%, rgba(255, 255, 255, 0.12), transparent 70%),
        radial-gradient(260px 140px at 50% 10%, rgba(255, 255, 255, 0.1), transparent 70%);
    opacity: 0.35;
    pointer-events: none;
    z-index: 0;
    animation: steam-rise 14s ease-in-out infinite;
}

.site-header-cover {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: saturate(0.9) contrast(0.95) brightness(0.7);
    z-index: 0;
}

.site-header-cover[src*="static.ghost.org"] {
    opacity: 0;
}

.site-header-content {
    position: relative;
    z-index: 100;
    display: flex;
    align-items: center;
    padding-top: calc(15vmin + 32px);
    padding-bottom: 14vmin;
    text-align: center;
    color: var(--color-coffee-cream);
    animation: hero-rise 0.9s var(--transition-slow);
}

.has-cover .site-header-content {
    min-height: 560px;
    background-color: transparent;
    color: var(--color-coffee-cream);
}

.site-header-content.left-aligned {
    padding-bottom: 0;
    text-align: left;
}

@keyframes hero-rise {
    from {
        opacity: 0;
        transform: translateY(18px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes steam-rise {
    0% {
        transform: translateY(0);
        opacity: 0.28;
    }
    50% {
        transform: translateY(-12px);
        opacity: 0.45;
    }
    100% {
        transform: translateY(0);
        opacity: 0.28;
    }
}

@keyframes card-rise {
    from {
        opacity: 0;
        transform: translateY(16px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@media (prefers-reduced-motion: reduce) {
    .site-header-content,
    .post-card {
        animation: none;
    }

    .site-header::after {
        animation: none;
    }

    .post-card,
    .gh-head-button {
        transition: none;
    }
}

.has-cover .site-header-content.left-aligned {
    align-items: flex-end;
    padding-bottom: max(4vmin, 32px);
}

.site-header-content.no-content {
    padding-top: 0;
    padding-bottom: 2vmin;
}

.site-header-inner {
    position: relative;
}

.site-header-content.left-aligned .site-header-inner {
    align-items: flex-start;
}

.site-logo {
    flex-shrink: 0;
    margin: 0 auto;
    max-height: 120px;
}

.site-header-content.left-aligned .site-logo {
    margin-right: auto;
    margin-left: 0;
    max-height: 96px;
}

.site-title {
    z-index: 10;
    margin: 0;
    padding: 0;
    font-size: 5rem;
    font-weight: 800;
    letter-spacing: -0.02em;
    text-shadow: 0 16px 40px rgba(26, 13, 0, 0.35);
}

.has-serif-title:not([class*=" gh-font-heading"]):not([class^="gh-font-heading"]) .site-title {
    font-family: var(--gh-font-heading, var(--font-serif));
}

.has-cover .site-title {
    font-size: 6rem;
}

.site-header-content.left-aligned .site-title {
    font-size: 4.4rem;
}

.has-cover .site-header-content.left-aligned .site-title {
    font-size: 4.6rem;
}

.site-description {
    display: inline-block;
    z-index: 10;
    max-width: 960px;
    font-size: 6rem;
    font-weight: 700;
    line-height: 1.1;
    color: var(--color-coffee-haze);
}

.site-description:first-child {
    font-family: var(--gh-font-heading, var(--font-sans));
}

.has-serif-title:not([class*=" gh-font-heading"]):not([class^="gh-font-heading"]) .site-description:first-child {
    font-family: var(--gh-font-heading, var(--font-serif));
}

:is(.site-logo, .site-title) + .site-description {
    max-width: 640px;
    margin-top: 16px;
    font-size: 2.4rem;
    font-weight: 400;
    line-height: 1.4;
}

.site-logo + .site-description {
    margin-top: 20px;
}

.site-title + .site-description {
    color: var(--color-coffee-cream);
}

.has-cover .site-description {
    letter-spacing: -0.005em;
    color: #fff;
}

.has-cover :is(.site-logo, .site-title) + .site-description {
    font-size: 2.4rem;
}

.has-cover .site-header-content.left-aligned :is(.site-logo, .site-title) + .site-description {
    font-size: 2.2rem;
}

@media (min-width: 992px) {
    .is-head-stacked.has-cover .site-header-content {
        padding-top: calc(19vmin + 120px);
    }
}

@media (max-width: 991px) {
    .site-header-content {
        padding-top: calc(14vmin + 24px);
        padding-bottom: 12vmin;
    }
}

@media (max-width: 767px) {
    .has-cover .site-header-content {
        min-height: 240px;
    }
    .site-header-inner {
        gap: 16px;
    }
    .site-logo {
        max-width: 60%;
    }
    .site-title {
        font-size: 3.4rem !important;
    }
    .site-description {
        font-size: 2.2rem !important;
    }
    .site-logo + .site-description {
        margin-top: 12px !important;
    }
    .site-title + .site-description {
        margin-top: 12px !important;
    }
}

.home-template .site-header-content {
    text-align: left;
    align-items: stretch;
}

.hero-grid {
    display: grid;
    grid-template-columns: minmax(0, 1.15fr) minmax(0, 0.85fr);
    gap: clamp(24px, 4vmin, 60px);
    align-items: center;
}

.hero-content {
    max-width: 620px;
}

.hero-kicker {
    text-transform: uppercase;
    letter-spacing: 0.28em;
    font-size: 1.1rem;
    font-weight: 600;
    color: rgba(253, 247, 239, 0.7);
    margin-bottom: 14px;
}

.hero-brand {
    display: flex;
    align-items: center;
    gap: 16px;
}

.home-template .site-title {
    font-size: clamp(3.6rem, 5vw, 6.6rem);
    margin-bottom: 12px;
}

.hero-description {
    font-size: clamp(1.8rem, 2.2vw, 2.6rem);
    font-weight: 400;
    line-height: 1.5;
    max-width: 52ch;
    margin-top: 10px;
}

.hero-actions {
    display: flex;
    flex-wrap: wrap;
    gap: 14px;
    margin-top: 24px;
}

.hero-button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 12px 26px;
    border-radius: 999px;
    background: linear-gradient(135deg, var(--color-coffee-accent), #F7B36A);
    color: #1a0d00;
    font-weight: 600;
    letter-spacing: -0.01em;
    box-shadow: 0 18px 30px rgba(230, 126, 34, 0.28);
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.hero-button--ghost {
    background: rgba(253, 247, 239, 0.14);
    color: var(--color-coffee-cream);
    border: 1px solid rgba(253, 247, 239, 0.35);
    box-shadow: none;
}

.hero-button:hover {
    transform: translateY(-2px);
    box-shadow: 0 22px 40px rgba(230, 126, 34, 0.32);
}

.hero-button--ghost:hover {
    box-shadow: 0 16px 30px rgba(0, 0, 0, 0.18);
}

.hero-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-top: 22px;
    font-size: 1.2rem;
    text-transform: uppercase;
    letter-spacing: 0.14em;
    color: rgba(253, 247, 239, 0.6);
}

.hero-tags span {
    padding: 6px 12px;
    border-radius: 999px;
    border: 1px solid rgba(253, 247, 239, 0.2);
    background: rgba(26, 13, 0, 0.2);
}

.hero-panel {
    display: grid;
    gap: 18px;
}

.hero-panel-card {
    position: relative;
    padding: 24px;
    border-radius: 24px;
    background: rgba(253, 247, 239, 0.12);
    border: 1px solid rgba(253, 247, 239, 0.18);
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.25);
    overflow: hidden;
}

.hero-panel-card::before {
    content: "";
    position: absolute;
    width: 140px;
    height: 140px;
    border-radius: 50%;
    border: 1px solid rgba(253, 247, 239, 0.2);
    top: -30px;
    right: -20px;
    opacity: 0.4;
}

.hero-panel-card--secondary {
    background: rgba(26, 13, 0, 0.35);
    border-color: rgba(253, 247, 239, 0.12);
}

.hero-panel-kicker {
    text-transform: uppercase;
    letter-spacing: 0.2em;
    font-size: 1rem;
    color: rgba(253, 247, 239, 0.6);
    margin-bottom: 12px;
}

.hero-panel-title {
    font-size: 2.4rem;
    line-height: 1.2;
    margin-bottom: 10px;
    color: var(--color-coffee-foam);
    font-family: var(--gh-font-heading, var(--font-heading));
}

.hero-panel-excerpt,
.hero-panel-text {
    font-size: 1.4rem;
    color: rgba(253, 247, 239, 0.75);
    line-height: 1.5;
}

.hero-panel-link {
    display: inline-flex;
    margin-top: 16px;
    font-size: 1.3rem;
    font-weight: 600;
    color: var(--color-coffee-glow);
}

@media (max-width: 991px) {
    .hero-grid {
        grid-template-columns: 1fr;
    }

    .hero-panel {
        order: 2;
    }
}

@media (max-width: 767px) {
    .hero-actions {
        flex-direction: column;
        align-items: flex-start;
    }

    .hero-panel-card {
        padding: 20px;
    }
}


/* 4. Site Navigation
/* ---------------------------------------------------------- */

.gh-head {
    height: 88px;
    font-size: 1.6rem;
    line-height: 1.3em;
    background-color: rgba(36, 21, 15, 0.94);
    color: var(--color-coffee-cream);
    border-bottom: 1px solid rgba(253, 247, 239, 0.08);
    backdrop-filter: blur(12px);
    box-shadow: 0 14px 28px rgba(20, 10, 6, 0.35);
    z-index: 150;
}

.has-cover:not(.home-template) .gh-head {
    background-color: rgba(36, 21, 15, 0.94);
    color: var(--color-coffee-cream);
}

:is(.home-template, .paged:not(.tag-template):not(.author-template)).has-cover .gh-head {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 2000;
    background-color: rgba(36, 21, 15, 0.35);
    color: #fff;
}

.gh-head a {
    text-decoration: none;
}

.gh-head-inner {
    display: grid;
    column-gap: 40px;
    grid-template-columns: auto 1fr auto;
    grid-auto-flow: row dense;
    align-items: center;
    height: 100%;
}

/* Header styles
/* ---------------------------------------------------------- */

.is-head-left-logo .gh-head-inner {
    grid-template-columns: auto 1fr auto;
}

.is-head-left-logo.home-template .gh-head:not(.is-header-hidden) .gh-head-logo {
    display: none;
}

.is-head-left-logo.home-template .gh-head:not(.is-header-hidden) .gh-head-menu {
    margin-left: -40px;
}

@media (min-width: 992px) {
    .is-head-left-logo .gh-head-menu {
        margin-right: 64px;
        margin-left: 16px;
    }
}

.is-head-middle-logo .gh-head-inner {
    grid-template-columns: 1fr auto 1fr;
}

.is-head-middle-logo .gh-head-brand {
    grid-column-start: 2;
}

@media (min-width: 992px) {
    .is-head-middle-logo .gh-head-menu {
        margin-right: 64px;
    }
}

.is-head-stacked .gh-head {
    height: auto;
}

.is-head-stacked .gh-head-inner {
    grid-template-columns: 1fr auto 1fr;
}

.is-head-stacked .gh-head-brand {
    grid-row-start: 1;
    grid-column-start: 2;
}

@media (min-width: 992px) {
    .is-head-stacked .gh-head-inner {
        padding: 0;
    }

    .is-head-stacked .gh-head-brand {
        position: relative;
        display: flex;
        align-items: center;
        height: 80px;
    }

    .is-head-stacked .gh-head-menu {
        grid-row-start: 2;
        grid-column: 1 / 4;
        justify-content: center;
        height: 56px;
        margin: 0 48px;
    }

    .is-head-stacked .gh-head-menu::before,
    .is-head-stacked .gh-head-menu::after {
        position: absolute;
        top: 80px;
        left: 0;
        width: 100%;
        height: 1px;
        content: "";
        background-color: var(--color-lightgrey);
    }

    .is-head-stacked.has-cover .gh-head-menu::before,
    .is-head-stacked.has-cover .gh-head-menu::after {
        background-color: rgba(255, 255, 255, 0.2);
    }

    .is-head-stacked .gh-head-menu::after {
        top: 136px;
    }

    .is-head-stacked .gh-head-actions {
        grid-row-start: 1;
        grid-column: 1 / 4;
        justify-content: space-between;
    }
}

/* Brand
/* ---------------------------------------------------------- */

.gh-head-brand {
    display: flex;
    align-items: center;
    height: 40px;
    word-break: break-all;
}

.gh-head-logo {
    display: block;
    font-family: var(--gh-font-heading, var(--font-sans));
    font-weight: 800;
    font-size: 2.6rem;
    letter-spacing: -0.02em;
    color: inherit;
    white-space: nowrap;
}

.gh-head-logo.no-image {
    margin-top: -5px;
}

.has-cover .gh-head-logo {
    color: #fff;
}

.gh-head-logo img {
    max-height: 40px;
}


/* Primary Navigation
/* ---------------------------------------------------------- */

.gh-head-menu {
    display: flex;
    align-items: center;
    margin-top: 1px;
    font-weight: 500;
}

.gh-head-menu .nav {
    display: inline-flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 32px;
    list-style: none;
    margin: 0;
    padding: 0;
}

.gh-head-menu .nav li {
    margin: 0;
    padding: 0;
}

.gh-head-menu .nav a {
    display: inline-block;
    line-height: 1.7;
    color: inherit;
    position: relative;
}

.gh-head-menu .nav a:hover {
    opacity: 1;
    color: var(--color-coffee-accent);
}

.gh-head-menu .nav .nav-current a::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -6px;
    width: 100%;
    height: 2px;
    border-radius: 999px;
    background: var(--color-coffee-accent);
}

.gh-head-menu .nav-more-toggle {
    position: relative;
    width: 30px;
    height: 30px;
    margin: 0 -6px;
    padding: 0;
    font-size: inherit;
    background-color: transparent;
    text-transform: inherit;
}

.gh-head-menu .nav-more-toggle svg {
    width: 24px;
    height: 24px;
}

@media (min-width: 992px) {
    body:not(.is-dropdown-loaded) .gh-head-menu .nav > li {
        opacity: 0;
    }
}


/* Dropdown
/* ---------------------------------------------------------- */

.gh-dropdown {
    position: absolute;
    top: 100%;
    right: -16px;
    z-index: 90;
    width: 200px;
    padding: 12px 0;
    margin-top: 24px;
    opacity: 0;
    visibility: hidden;
    text-align: left;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.04), 0 7px 20px -5px rgba(0, 0, 0, 0.15);
    transform: translate3d(0, 6px, 0);
    transition: opacity 0.3s, transform 0.2s;
}

.is-head-middle-logo .gh-dropdown {
    right: auto;
    left: -24px;
}

.is-dropdown-mega .gh-dropdown {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-auto-flow: column;
    column-gap: 40px;
    min-width: 320px;
    padding: 20px 32px;
}

.is-dropdown-open .gh-dropdown {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
}

.gh-head-menu .gh-dropdown li a {
    display: block;
    padding: 6px 20px;
    color: #15171a;
}

.is-dropdown-mega .gh-dropdown li a {
    padding: 8px 0;
}


/* Secondary Navigation
/* ---------------------------------------------------------- */

.gh-social {
    display: flex;
    align-items: center;
    gap: 20px;
}

.gh-social-link {
    line-height: 0;
    color: inherit;
}

.gh-social-link:hover {
    opacity: 0.9;
}

.gh-social-link svg {
    width: 18px;
    height: 18px;
}

.gh-head-actions {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 24px;
    list-style: none;
    text-align: right;
}

.gh-head-members {
    display: flex;
    gap: 20px;
    align-items: center;
}

.gh-head-link {
    font-weight: 500;
    color: inherit;
}

.gh-head-button {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    padding: 8px 20px;
    height: 44px;
    font-weight: 600;
    letter-spacing: -0.005em;
    font-size: 1.6rem;
    border-radius: 48px;
    color: #fff;
    background: linear-gradient(135deg, var(--color-coffee-accent), #F09D4A);
    box-shadow: 0 12px 24px rgba(230, 126, 34, 0.25);
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.has-cover .gh-head-button {
    color: var(--color-darkgrey);
    background: #fff;
}

.gh-head-button:hover {
    transform: translateY(-1px);
    box-shadow: 0 16px 30px rgba(230, 126, 34, 0.3);
}

@media (max-width: 767px) {
    .gh-head-members {
        flex-direction: column-reverse;
        gap: 16px;
        width: 100%;
    }
}


/* Search
/* ---------------------------------------------------------- */

.gh-search {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    padding: 0;
    cursor: pointer;
    background-color: transparent;
    border: 0;
    outline: none;
}

.gh-search:hover {
    opacity: 0.9;
}

.gh-head-brand .gh-search {
    margin-right: 8px;
}

.gh-head-actions .gh-search {
    margin-right: -4px;
}

@media (max-width: 767px) {
    .gh-head-actions .gh-search {
        display: none;
    }
}

@media (min-width: 768px) {
    .gh-head-brand .gh-search {
        display: none;
    }
}


/* Mobile Menu Trigger
/* ---------------------------------------------------------- */

.gh-burger {
    position: relative;
    display: none;
    width: 30px;
    height: 30px;
    padding: 0;
    margin-right: -3px;
    cursor: pointer;
    background-color: transparent;
    border: 0;
    appearance: none;
}

.gh-burger::before,
.gh-burger::after {
    position: absolute;
    left: 3px;
    width: 24px;
    height: 1px;
    content: "";
    background-color: var(--color-darkgrey);
    transition: all 0.2s cubic-bezier(0.04, 0.04, 0.12, 0.96) 0.1008s;
}

.has-cover .gh-burger::before,
.has-cover .gh-burger::after {
    background-color: var(--color-coffee-foam);
}

.gh-burger::before {
    top: 11px;
}

.gh-burger::after {
    bottom: 11px;
}

.gh-head-open .gh-burger::before {
    top: 15px;
    transform: rotate(45deg);
}

.gh-head-open .gh-burger::after {
    bottom: 14px;
    transform: rotate(-45deg);
}


/* Mobile Menu
/* ---------------------------------------------------------- */
/* IDs needed to ensure sufficient specificity */

@media (max-width: 767px) {
    #gh-head {
        height: 64px;
    }

    #gh-head .gh-head-inner {
        grid-template-rows: auto 1fr auto;
        grid-template-columns: 1fr;
        gap: 48px;
    }

    #gh-head .gh-head-brand {
        display: grid;
        grid-template-columns: 1fr auto auto;
        grid-column-start: 1;
        align-items: center;
        height: 64px;
    }

    #gh-head .gh-head-logo {
        font-size: 2.2rem;
    }

    #gh-head .gh-head-brand .gh-search {
        margin-left: -6px;
    }

    #gh-head .gh-burger {
        display: block;
    }

    #gh-head .gh-head-menu,
    #gh-head .gh-head-actions {
        position: fixed;
        justify-content: center;
        visibility: hidden;
        opacity: 0;
    }

    #gh-head .gh-head-menu {
        margin: 0;
        transition: none;
        transform: translateY(0);
    }

    #gh-head .nav {
        gap: 16px;
        align-items: center;
        line-height: 1.4;
    }

    #gh-head .nav a {
        font-size: 2.6rem;
        font-weight: 600;
        text-transform: none;
    }

    #gh-head .nav li {
        opacity: 0;
        transform: translateY(-4px);
    }

    #gh-head :is(.gh-head-button, .gh-head-link) {
        opacity: 0;
        transform: translateY(8px);
    }

    #gh-head .gh-head-button {
        width: 100%;
        font-size: 1.8rem;
        text-transform: none;
        opacity: 0;
        transform: translateY(8px);
    }

    .gh-head-open #gh-head {
        position: fixed;
        inset: 0;
        z-index: 3999999;
        height: 100%;
        overflow-y: scroll;
        -webkit-overflow-scrolling: touch;
    }

    .gh-head-open.has-cover #gh-head,
    .gh-head-open.has-cover #gh-head .gh-head-actions {
        background-color: var(--ghost-accent-color);
    }

    .gh-head-open #gh-head .gh-head-menu,
    .gh-head-open #gh-head .gh-head-actions {
        position: static;
        visibility: visible;
        opacity: 1;
    }

    .gh-head-open #gh-head .nav {
        display: flex;
        flex-direction: column;
    }

    .gh-head-open #gh-head .nav li {
        opacity: 1;
        transition: transform 0.2s, opacity 0.2s;
        transform: translateY(0);
    }

    .gh-head-open #gh-head .gh-head-actions {
        position: sticky;
        right: 0;
        bottom: 0;
        left: 0;
        display: inline-flex;
        flex-direction: column;
        gap: 12px;
        align-items: center;
        padding: max(4vmin, 20px) 0 max(4vmin, 28px);
        background-color: var(--color-coffee-foam);
    }

    .gh-head-open #gh-head :is(.gh-head-button, .gh-head-link) {
        opacity: 1;
        transition: transform 0.4s, opacity 0.4s;
        transition-delay: 0.2s;
        transform: translateY(0);
    }

    .gh-head-open #gh-head .gh-head-link {
        transition-delay: 0.4s;
    }
}


/* Brew Bar + Collections
/* ---------------------------------------------------------- */

.brew-bar {
    position: relative;
    margin-top: -5vmin;
    padding-bottom: max(3vmin, 24px);
    z-index: 3;
}

.brew-bar-inner {
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    gap: 18px;
    padding: clamp(24px, 4vmin, 36px);
    border-radius: 28px;
    background:
        radial-gradient(280px 140px at 10% 0%, rgba(255, 185, 120, 0.18), transparent 60%),
        radial-gradient(240px 140px at 90% 20%, rgba(255, 255, 255, 0.12), transparent 60%),
        linear-gradient(135deg, #2D1810 0%, #3B2218 55%, #4A3228 100%);
    box-shadow: 0 24px 50px rgba(26, 13, 0, 0.35);
}

.brew-tile {
    position: relative;
    padding: 22px;
    border-radius: 20px;
    background: rgba(253, 247, 239, 0.08);
    border: 1px solid rgba(253, 247, 239, 0.14);
    color: var(--color-coffee-foam);
    transition: transform 0.25s ease, box-shadow 0.25s ease;
}

.brew-tile::before {
    content: "";
    position: absolute;
    inset: 12px;
    border-radius: 18px;
    border: 1px dashed rgba(253, 247, 239, 0.18);
    opacity: 0;
    transition: opacity 0.25s ease;
}

.brew-tile:hover {
    transform: translateY(-4px);
    box-shadow: 0 16px 30px rgba(0, 0, 0, 0.28);
}

.brew-tile:hover::before {
    opacity: 0.7;
}

.brew-tile-kicker {
    font-size: 1.1rem;
    text-transform: uppercase;
    letter-spacing: 0.2em;
    color: rgba(253, 247, 239, 0.6);
    margin-bottom: 10px;
}

.brew-tile-title {
    font-size: 2.2rem;
    margin-bottom: 8px;
    font-family: var(--gh-font-heading, var(--font-heading));
}

.brew-tile-text {
    font-size: 1.4rem;
    color: rgba(253, 247, 239, 0.75);
    line-height: 1.5;
}

.collection-actions {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-top: 16px;
}

.collection-chip {
    display: inline-flex;
    align-items: center;
    padding: 6px 14px;
    border-radius: 999px;
    font-size: 1.1rem;
    text-transform: uppercase;
    letter-spacing: 0.16em;
    color: var(--color-coffee-dark);
    background: rgba(255, 255, 255, 0.6);
    border: 1px solid rgba(74, 50, 40, 0.12);
}

.collection-grid {
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    gap: 22px;
    margin: max(4vmin, 32px) 0;
}

.collection-card {
    padding: 24px;
    border-radius: 22px;
    background: var(--color-coffee-foam);
    border: 1px solid rgba(74, 50, 40, 0.08);
    box-shadow: 0 14px 30px rgba(45, 24, 16, 0.1);
}

.collection-card-kicker {
    font-size: 1.1rem;
    letter-spacing: 0.18em;
    text-transform: uppercase;
    color: var(--color-coffee-caramel);
    margin-bottom: 10px;
}

.collection-card-title {
    font-size: 2.2rem;
    margin-bottom: 10px;
    font-family: var(--gh-font-heading, var(--font-heading));
}

.collection-card-text {
    font-size: 1.5rem;
    color: var(--color-coffee-medium);
    line-height: 1.6;
}

.talks-intro {
    margin: max(4vmin, 32px) 0;
}

.talks-intro-grid {
    display: grid;
    grid-template-columns: minmax(0, 1.1fr) minmax(0, 0.9fr);
    gap: 24px;
}

.talks-intro-title {
    font-size: 3rem;
    margin-bottom: 12px;
    font-family: var(--gh-font-heading, var(--font-heading));
    color: var(--color-coffee-dark);
}

.talks-intro-text {
    font-size: 1.6rem;
    color: var(--color-coffee-medium);
    line-height: 1.6;
}

.talks-topics,
.talks-highlight-list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-top: 16px;
}

.talks-topics span,
.talks-highlight-list span {
    padding: 6px 12px;
    border-radius: 999px;
    font-size: 1.2rem;
    border: 1px solid rgba(74, 50, 40, 0.15);
    background: rgba(255, 255, 255, 0.6);
}

.talks-highlight {
    padding: 22px;
    border-radius: 22px;
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.8), rgba(253, 247, 239, 0.6));
    border: 1px solid rgba(74, 50, 40, 0.08);
    box-shadow: 0 16px 30px rgba(45, 24, 16, 0.12);
}

.talks-highlight-kicker {
    text-transform: uppercase;
    letter-spacing: 0.2em;
    font-size: 1rem;
    color: var(--color-coffee-caramel);
    margin-bottom: 10px;
}

.talks-highlight-text {
    font-size: 1.5rem;
    color: var(--color-coffee-dark);
    line-height: 1.6;
}

.talks-grid {
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    gap: 20px;
    margin: max(4vmin, 32px) 0;
}

.talk-card {
    padding: 22px;
    border-radius: 22px;
    background: var(--color-coffee-foam);
    border: 1px solid rgba(74, 50, 40, 0.08);
    box-shadow: var(--shadow-card);
}

.talk-card-kicker {
    font-size: 1.1rem;
    text-transform: uppercase;
    letter-spacing: 0.18em;
    color: var(--color-coffee-caramel);
    margin-bottom: 10px;
}

.talk-card-title {
    font-size: 2.2rem;
    margin-bottom: 10px;
    font-family: var(--gh-font-heading, var(--font-heading));
}

.talk-card-text {
    font-size: 1.5rem;
    color: var(--color-coffee-medium);
    line-height: 1.6;
}

.talks-feed {
    margin-top: max(4vmin, 32px);
}

.talks-feed-header {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 24px;
}

.talks-feed-header h2 {
    font-size: 3rem;
    margin: 0;
    font-family: var(--gh-font-heading, var(--font-heading));
}

.talks-feed-header p {
    font-size: 1.6rem;
    color: var(--color-coffee-medium);
}

.talks-feed-grid .post-card {
    background: #fff;
}

.talks-empty {
    padding: 28px;
    border-radius: 20px;
    background: rgba(255, 255, 255, 0.7);
    border: 1px dashed rgba(74, 50, 40, 0.18);
    color: var(--color-coffee-medium);
}

@media (max-width: 991px) {
    .brew-bar-inner,
    .collection-grid,
    .talks-grid {
        grid-template-columns: 1fr;
    }

    .talks-intro-grid {
        grid-template-columns: 1fr;
    }
}

@media (max-width: 767px) {
    .brew-bar {
        margin-top: -4vmin;
    }
}


/* 5. Post Feed
/* ---------------------------------------------------------- */

.post-feed {
    position: relative;
    display: grid;
    gap: 4.8vmin 4vmin;
    grid-template-columns: repeat(6, 1fr);
    padding: max(4.8vmin, 36px) 0 0;
}

:is(.tag-template, .author-template) .post-feed {
    margin-top: 4vmin;
}

@media (max-width: 991px) {
    .post-feed {
        grid-template-columns: 1fr 1fr;
    }
}

@media (max-width: 767px) {
    .post-feed {
        grid-template-columns: 1fr;
        grid-gap: 40px;
    }
}

.post-feed-cta {
    display: flex;
    justify-content: center;
    margin: max(4vmin, 32px) 0 0;
}

.post-feed-cta .hero-button--ghost {
    color: var(--color-coffee-dark);
    background: rgba(45, 24, 16, 0.06);
    border: 1px solid rgba(74, 50, 40, 0.2);
    box-shadow: none;
}

.post-feed-cta .hero-button--ghost:hover {
    box-shadow: 0 18px 30px rgba(45, 24, 16, 0.18);
}

.collection-hero {
    position: relative;
    margin: max(6vmin, 40px) 0 max(3.6vmin, 28px);
    padding: max(4vmin, 32px) max(4vmin, 32px);
    border-radius: 24px;
    border: 1px solid rgba(74, 50, 40, 0.08);
    background:
        radial-gradient(260px 180px at 15% 20%, rgba(255, 255, 255, 0.65), transparent 70%),
        radial-gradient(280px 200px at 85% 0%, rgba(255, 185, 120, 0.2), transparent 60%),
        linear-gradient(135deg, var(--color-coffee-cream) 0%, var(--color-coffee-foam) 55%, #ffffff 100%);
    box-shadow: var(--shadow-soft);
    overflow: hidden;
}

.collection-hero::after {
    content: "";
    position: absolute;
    inset: 0;
    background:
        radial-gradient(200px 80px at 10% 30%, rgba(255, 255, 255, 0.4), transparent 70%),
        radial-gradient(220px 90px at 90% 20%, rgba(255, 255, 255, 0.35), transparent 70%);
    opacity: 0.6;
    pointer-events: none;
}

.collection-hero > * {
    position: relative;
    z-index: 1;
}

.collection-kicker {
    text-transform: uppercase;
    letter-spacing: 0.2em;
    font-size: 1.1rem;
    font-weight: 600;
    color: var(--color-coffee-caramel);
}

.collection-title {
    margin-top: 12px;
    font-size: clamp(3.2rem, 4vw, 4.4rem);
    font-weight: 700;
    color: var(--color-coffee-dark);
    font-family: var(--gh-font-heading, var(--font-heading));
}

.collection-description {
    margin-top: 12px;
    max-width: 60ch;
    font-size: 1.8rem;
    color: var(--color-coffee-medium);
}

.collection-content {
    margin-top: 24px;
}

.post-card {
    position: relative;
    grid-column: span 2;
    display: flex;
    flex-direction: column;
    background: var(--color-coffee-foam);
    border-radius: var(--radius-card);
    box-shadow: var(--shadow-card);
    padding: 24px;
    background-size: cover;
    word-break: break-word;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    animation: card-rise 0.7s var(--transition-slow) both;
}

.post-card::before {
    content: "";
    position: absolute;
    width: 110px;
    height: 110px;
    right: 18px;
    bottom: 18px;
    border-radius: 50%;
    border: 1px solid rgba(74, 50, 40, 0.16);
    opacity: 0.35;
    pointer-events: none;
    transition: transform 0.3s ease, opacity 0.3s ease;
}

.post-card:nth-child(1) { animation-delay: 0.05s; }
.post-card:nth-child(2) { animation-delay: 0.1s; }
.post-card:nth-child(3) { animation-delay: 0.15s; }
.post-card:nth-child(4) { animation-delay: 0.2s; }
.post-card:nth-child(5) { animation-delay: 0.25s; }
.post-card:nth-child(6) { animation-delay: 0.3s; }
.post-card:nth-child(7) { animation-delay: 0.35s; }
.post-card:nth-child(8) { animation-delay: 0.4s; }

.post-card:hover {
    transform: translateY(-4px);
    box-shadow: var(--shadow-card-hover);
}

.post-card:hover::before {
    transform: translateY(-4px) scale(1.02);
    opacity: 0.55;
}

.post-card-image-link {
    position: relative;
    overflow: hidden;
    display: block;
    margin: -24px -24px 24px;
    border-radius: calc(var(--radius-card) - 4px);
}

.post-card-image-link--fallback {
    min-height: 180px;
}

.post-card-image-link::after {
    content: "";
    display: block;
    padding-bottom: 55%;
}

.post-card[class*="post-access-"] .post-card-image-link::after {
    background-color: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(3px);
}

.post-card.keep-ratio[class*="post-access-"] .post-card-image-link::after {
    position: absolute;
    inset: 0;
    padding-bottom: 0;
}

.post-card.keep-ratio:not(.post-card-large):not(.post-card-full) .post-card-image-link::after {
    padding-bottom: 0;
}

.post-card-image {
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    background: var(--color-lightgrey) no-repeat center center;
    object-fit: cover;
    transition: transform var(--transition-slow);
}

.post-card-image--fallback {
    background:
        radial-gradient(140px 90px at 18% 30%, rgba(255, 255, 255, 0.12), transparent 65%),
        radial-gradient(220px 140px at 78% 18%, rgba(255, 255, 255, 0.08), transparent 60%),
        linear-gradient(135deg, var(--color-coffee-dark) 0%, var(--color-coffee-medium) 55%, var(--color-coffee-caramel) 100%);
}

.post-card:hover .post-card-image {
    transform: scale(1.03);
}

.post-card.keep-ratio:not(.post-card-large):not(.post-card-full) .post-card-image {
    position: static;
}

.post-card-access {
    position: absolute;
    inset: 0;
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 4px;
    font-size: 1.5rem;
    font-weight: 600;
    color: #fff;
}

.post-card-content-link {
    position: relative;
    display: block;
    color: var(--color-darkgrey);
}

.post-card-content-link:hover {
    text-decoration: none;
}

.post-feed .no-image .post-card-content-link {
    padding: 0;
}

.no-image .post-card-header {
    margin-top: 0;
}

.post-card-tags {
    display: flex;
    align-items: center;
    gap: 12px;
    margin: 0 0 10px;
    color: var(--color-secondary-text);
    font-size: 1.4rem;
    font-weight: 600;
    letter-spacing: -0.005em;
    line-height: 1;
}

.post-card-primary-tag {
    padding: 4px 10px;
    border-radius: var(--radius-pill);
    background: rgba(230, 126, 34, 0.14);
    color: var(--color-coffee-accent);
    text-transform: uppercase;
    letter-spacing: 0.08em;
    font-size: 1.1rem;
}

.post-card-featured {
    position: relative;
    display: flex;
    align-items: center;
    gap: 3px;
    padding-left: 18px;
    color: var(--ghost-accent-color);
}

.post-card-featured svg {
    position: absolute;
    left: 0;
}

.post-card-title {
    margin: 0;
    font-size: 2.6rem;
    font-weight: 800;
    line-height: 1.2;
}

.post-card-content-link:hover .post-card-title {
    opacity: 0.9;
}

.no-image .post-card-title {
    margin-top: 0;
}

.has-serif-title:not([class*=" gh-font-heading"]):not([class^="gh-font-heading"]) .post-card-title {
    font-family: var(--gh-font-heading, var(--font-serif));
    letter-spacing: -0.005em;
}

.post-card-title svg {
    margin-top: -3px;
    margin-left: -1px;
}

.post-card-content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.post-card-excerpt {
    display: -webkit-box;
    overflow-y: hidden;
    margin-top: 12px;
    max-width: 720px;
    font-size: 1.6rem;
    line-height: 1.5;
    color: var(--color-coffee-medium);
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    word-break: break-word;
}

.has-sans-body:not([class*=" gh-font-body"]):not([class^="gh-font-body"]) .post-card-excerpt {
    font-family: var(--gh-font-body, var(--font-sans));
}

.post-card:not(.post-card-large):not(.post-card-full):not(.dynamic):not(.no-image) .post-card-excerpt {
    -webkit-line-clamp: 2;
}

:is(.tag-template, .author-template) .post-card-excerpt {
    margin-top: 6px;
}

:is(.tag-template, .author-template) .post-card-large .post-card-excerpt {
    display: block;
    overflow-y: auto;
}

.post-card-meta {
    display: flex;
    align-items: center;
    gap: 6px;
    margin-top: 12px;
    padding: 0;
    font-size: 1.3rem;
    color: var(--color-secondary-text);
}

.post-card-meta > * {
    display: flex;
    align-items: center;
    gap: 6px;
}

.post-card-meta > * + *:not(script)::before {
    width: 2px;
    height: 2px;
    content: "";
    background-color: var(--color-secondary-text);
    border-radius: 50%;
}

.post-card-meta .sep {
    margin: 0 4px;
}

.author-profile-image {
    display: block;
    width: 100%;
    height: 100%;
    background: color-mod(var(--color-lightgrey) l(+10%));
    border-radius: 100%;

    object-fit: cover;
}

.author-list {
    display: flex;
    margin: 0 0 0 4px;
    padding: 0;
    list-style: none;
}

.author-list-item {
    position: relative;
    flex-shrink: 0;
    margin: 0;
    padding: 0;
}


/* Special Styling for home page grid (below):

The first post in the list is styled to be bigger than the others and take over
the full width of the grid to give it more emphasis. Wrapped in a media query to
make sure this only happens on large viewports / desktop-ish devices.

*/

@media (min-width: 1001px) {
    .post-card-large {
        grid-column: span 6;
        display: grid;
        grid-gap: 4vmin;
        grid-template-columns: repeat(3, 1fr);
        border-top: 0;
        padding: 28px;
    }

    .post-card-large:not(.no-image) .post-card-header {
        margin-top: 0;
    }

    .post-card-large .post-card-image-link {
        position: relative;
        grid-column: span 2;
        margin: 0;
        border-radius: calc(var(--radius-card) - 4px);
    }

    .post-card-large .post-card-content {
        grid-column: span 1;
    }

    .post-card-large.no-image .post-card-content {
        grid-column: span 2;
    }

    .post-card-large .post-card-image {
        position: absolute;
        width: 100%;
        height: 100%;
    }

    .post-card-large .post-card-tags {
        margin-bottom: 12px;
    }

    .post-card-large .post-card-title {
        margin-top: 0;
        font-size: 4.4rem;
        line-height: 1.05;
    }

    .post-card-large .post-card-excerpt {
        margin-top: 16px;
    }

    .post-card-full {
        grid-column: span 6;
    }

    .post-card-full .post-card-image-link {
        margin-bottom: 40px;
    }

    .post-card-full .post-card-tags {
        margin-bottom: 14px;
    }

    .post-card-full .post-card-title {
        font-size: 6.4rem;
        line-height: 0.95;
    }

    .post-card-full .post-card-excerpt {
        margin-top: 20px;
        font-size: 1.8rem;
    }

    .post-card-large + .post-card-large:nth-child(even) {
        margin: 32px 0;
    }

    .post-card-large + .post-card-large:nth-child(even) .post-card-content {
        order: -1;
    }

    .post-card.dynamic {
        grid-column: span 3;
    }

    .post-card.dynamic .post-card-title {
        font-size: 3rem;
    }
}

/* Pagination
/* ---------------------------------------------------------- */

.pagination {
    display: none;
    grid-template-columns: 1fr auto 1fr;
    align-items: center;
    margin-top: 8vmin;
}

html.no-infinite-scroll .pagination {
    display: grid;
}

.pagination a {
    font-size: 1.7rem;
    font-weight: 600;
}

.pagination .page-number {
    grid-column-start: 2;
    color: var(--color-secondary-text);
}

.pagination .older-posts {
    grid-column-start: 3;
    text-align: right;
}

@media (max-width: 767px) {
    .pagination .page-number {
        display: none;
    }
}


/* 6. Single Post
/* ---------------------------------------------------------- */

.article {
    word-break: break-word;
}

.post-template .article {
    padding: max(8vmin, 40px) 0 max(8vmin, 64px);
}

.post-template .article-header {
    padding: 0 0 max(6.4vmin, 40px);
}

.page-template .article-header {
    padding: max(12vmin, 64px) 0 max(3.2vmin, 28px);
}

.article-tag {
    margin-bottom: 16px;
    font-size: 1.6rem;
}

.article-tag a {
    color: var(--color-secondary-text);
}

.article-title {
    margin-bottom: 0;
    font-size: clamp(3.2rem, 5vw, 5.2rem);
    font-weight: 800;
    line-height: 1.05;
    color: var(--color-darkgrey);
}

.has-serif-title:not([class*=" gh-font-heading"]):not([class^="gh-font-heading"]) .article-title {
    font-family: var(--gh-font-heading, var(--font-serif));
}

.article-excerpt {
    margin-top: 20px;
    max-width: 720px;
    font-size: 2rem;
    line-height: 1.45;
    color: var(--color-darkgrey);
}

.gh-canvas .article-image {
    grid-column: wide-start / wide-end;
    width: 100%;
    margin: max(6.4vmin, 40px) 0 0;
    border-radius: var(--radius-card);
    overflow: hidden;
    box-shadow: var(--shadow-card);
}

.image-full .article-image {
    grid-column: full-start / full-end;
}

.image-small .article-image {
    grid-column: main-start / main-end;
}

.gh-canvas .article-image img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
}

.article-image--fallback {
    position: relative;
    overflow: hidden;
    border-radius: var(--radius-card);
    aspect-ratio: 16 / 9;
    box-shadow: var(--shadow-card);
}

.article-image--fallback .post-card-image--fallback {
    position: absolute;
    inset: 0;
}

@media (max-width: 767px) {
    .article-excerpt {
        margin-top: 14px;
        font-size: 1.7rem;
    }
}

/* -------- */

/* Content grid
/* ---------------------------------------------------------- */

/* Canvas creates a multi-column, centered grid which the post
is laid out on top of. Canvas just defines the grid, we don't
use it for applying any other styles. */

.gh-canvas,
.kg-width-full.kg-content-wide {
    --gap: max(4vmin, 20px);
    --main: min(var(--content-width, 720px), 100% - var(--gap) * 2);
    --wide: minmax(0, calc((var(--container-width, 1200px) - var(--content-width, 720px)) / 2));
    --full: minmax(var(--gap), 1fr);

    display: grid;
    grid-template-columns:
        [full-start] var(--full)
        [wide-start] var(--wide)
        [main-start] var(--main) [main-end]
        var(--wide) [wide-end]
        var(--full) [full-end];
}

.gh-canvas > * {
    grid-column: main-start / main-end;
}

.kg-width-wide,
.kg-content-wide > div {
    grid-column: wide-start / wide-end;
}

.kg-width-full {
    grid-column: full-start / full-end;
}

.kg-width-full img {
    width: 100%;
}


/* Content
/* ---------------------------------------------------------- */

/* Content refers to styling all page and post content that is
created within the Ghost editor. The main content handles
headings, text, images and lists. We deal with cards lower down. */

/* Default vertical spacing */
.gh-content > * + * {
    margin-top: max(3.2vmin, 24px);
    margin-bottom: 0;
}

/* Remove space between full-width cards */
.gh-content > .kg-width-full + .kg-width-full:not(.kg-width-full.kg-card-hascaption + .kg-width-full) {
    margin-top: 0;
}

/* [id] represents all headings h1-h6, reset all margins */
.gh-content > [id] {
    margin: 0;
    color: var(--color-darkgrey);
}

.has-serif-title:not([class*=" gh-font-heading"]):not([class^="gh-font-heading"]) .gh-content > [id] {
    font-family: var(--gh-font-heading, var(--font-serif));
}

/* Add back a top margin to all headings, unless a heading
is the very first element in the post content */
.gh-content > [id]:not(:first-child) {
    margin: 2em 0 0;
}

/* Add a small margin between a heading and anything after it */
.gh-content > [id] + * {
    margin-top: 1.5rem !important;
}

/* A larger margin before/after HRs and blockquotes */
.gh-content > hr,
.gh-content > blockquote {
    position: relative;
    margin-top: max(4.8vmin, 32px);
}
.gh-content > hr + *,
.gh-content > blockquote + * {
    margin-top: max(4.8vmin, 32px) !important;
}

/* Now the content typography styles */
.gh-content a {
    color: var(--ghost-accent-color);
    text-decoration: underline;
    text-decoration-color: rgba(230, 126, 34, 0.35);
    text-underline-offset: 3px;
    transition: color 0.2s ease, text-decoration-color 0.2s ease;
    word-break: break-word;
}

.gh-content a:hover {
    color: #d86c17;
    text-decoration-color: var(--ghost-accent-color);
}

.gh-content > blockquote {
    border-left: 4px solid var(--color-coffee-accent);
    background: rgba(253, 247, 239, 0.7);
    padding: 20px 24px;
    border-radius: 12px;
    box-shadow: 0 12px 26px rgba(45, 24, 16, 0.08);
}

.gh-content > blockquote:not([class]),
.gh-content > ol,
.gh-content > ul,
.gh-content > dl,
.gh-content > p {
    font-family: var(--gh-font-body, var(--font-serif));
    font-weight: 400;
    font-size: 2rem;
    line-height: 1.6em;
}

.gh-content > p img {
  margin: 0 auto;
}

.page-template .gh-content:only-child > *:first-child:not(.kg-width-full) {
    margin-top: max(12vmin, 64px);
}

.page-template .gh-content > *:last-child:not(.kg-width-full) {
    margin-bottom: max(12vmin, 64px);
}

.gh-content .kg-callout-card .kg-callout-text,
.gh-content .kg-toggle-card .kg-toggle-content > ol,
.gh-content .kg-toggle-card .kg-toggle-content > ul,
.gh-content .kg-toggle-card .kg-toggle-content > p {
    font-family: var(--font-serif);
    font-weight: 400;
    font-size: 1.9rem;
    line-height: 1.6em;
}

.gh-content .kg-product-card .kg-product-card-description > p,
.gh-content .kg-product-card .kg-product-card-description > ol,
.gh-content .kg-product-card .kg-product-card-description > ul {
    font-size: 1.7rem;
    line-height: 1.6em;
}

.gh-content .kg-callout-card .kg-callout-emoji {
    font-size: 2.1rem;
    line-height: 1.4em;
}

.gh-content .kg-toggle-card .kg-toggle-heading-text {
    font-size: 2.0rem;
}

.has-sans-body:not([class*=" gh-font-body"]):not([class^="gh-font-body"]) .gh-content > blockquote,
.has-sans-body:not([class*=" gh-font-body"]):not([class^="gh-font-body"]) .gh-content > ol,
.has-sans-body:not([class*=" gh-font-body"]):not([class^="gh-font-body"]) .gh-content > ul,
.has-sans-body:not([class*=" gh-font-body"]):not([class^="gh-font-body"]) .gh-content > dl,
.has-sans-body:not([class*=" gh-font-body"]):not([class^="gh-font-body"]) .gh-content > p,
.has-sans-body:not([class*=" gh-font-body"]):not([class^="gh-font-body"]) .gh-content .kg-callout-card .kg-callout-text,
.has-sans-body:not([class*=" gh-font-body"]):not([class^="gh-font-body"]) .gh-content .kg-toggle-card .kg-toggle-content > ol,
.has-sans-body:not([class*=" gh-font-body"]):not([class^="gh-font-body"]) .gh-content .kg-toggle-card .kg-toggle-content > ul,
.has-sans-body:not([class*=" gh-font-body"]):not([class^="gh-font-body"]) .gh-content .kg-toggle-card .kg-toggle-content > p {
    font-family: var(--gh-font-body, var(--font-sans));
}

.gh-content > ul,
.gh-content > ol,
.gh-content > dl,
.gh-content .kg-toggle-card .kg-toggle-content > ol,
.gh-content .kg-toggle-card .kg-toggle-content > ul,
.gh-content .kg-product-card .kg-product-card-description > ol,
.gh-content .kg-product-card .kg-product-card-description > ul {
    padding-left: 1.9em;
}

.gh-content > blockquote:not([class]) {
    position: relative;
    font-style: italic;
    padding: 1.5rem 2rem;
    margin: 2rem 0;
    background: var(--color-coffee-foam, #FDF7EF);
    border-radius: 8px;
    border-left: 4px solid var(--ghost-accent-color);
}

.gh-content > blockquote:not([class])::before {
    display: none;
}

.gh-content :not(pre) > code {
    vertical-align: middle;
    padding: 0.2em 0.5em;
    border: none;
    font-weight: 500 !important;
    font-size: 0.875em;
    line-height: 1.4em;
    color: var(--color-coffee-dark, #2D1810);
    background: var(--color-coffee-haze, #F8EDE0);
    border-radius: 4px;
    box-shadow: inset 0 0 0 1px rgba(45, 24, 16, 0.1);
}

.gh-content pre {
    overflow: auto;
    padding: 1.25rem 1.5rem;
    color: var(--color-coffee-cream, #F5E6D3);
    font-size: 1.4rem;
    line-height: 1.6em;
    background: var(--color-coffee-dark, #2D1810);
    border-radius: 10px;
    box-shadow: 0 4px 16px rgba(26, 13, 0, 0.15), 0 0 0 1px rgba(196, 168, 130, 0.1);
    margin: 2rem 0;
}

.gh-content ol ol li {
    list-style-type: lower-alpha;
}

.gh-content ol ol ol li {
    list-style-type: lower-roman;
}

@media (max-width: 650px) {
    .gh-content > blockquote:not([class]),
    .gh-content > ol,
    .gh-content > ul,
    .gh-content > dl,
    .gh-content > p,
    .gh-content .kg-callout-card .kg-callout-text,
    .gh-content .kg-toggle-card .kg-toggle-content > ol,
    .gh-content .kg-toggle-card .kg-toggle-content > ul,
    .gh-content .kg-toggle-card .kg-toggle-content > p {
        font-size: 1.8rem;
    }

    .gh-content .kg-product-card .kg-product-card-description > p,
    .gh-content .kg-product-card .kg-product-card-description > ol,
    .gh-content .kg-product-card .kg-product-card-description > ul {
        font-size: 1.6rem;
    }

    .gh-content blockquote:not([class])::before {
        left: min(-4vmin, -20px);
    }
}


/* Cards
/* ---------------------------------------------------------- */

/* Cards are dynamic blocks of content which appear within Ghost
posts, for example: embedded videos, tweets, galleries, or
specially styled bookmark links. We add extra styling here to
make sure they look good, and are given a bit of extra spacing. */

/* Add extra margin before/after any cards,
except for when immediately preceeded by a heading */
.gh-content :not(.kg-card):not([id]) + .kg-card {
    margin-top: 6vmin;
}
.gh-content .kg-card + :not(.kg-card) {
    margin-top: 6vmin;
}

/* This keeps small embeds centered */
.kg-embed-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

/* This keeps small iamges centered */
.kg-image-card img {
    margin: auto;
}

.kg-image[width][height],
.kg-gallery-image {
    cursor: pointer;
}

.has-serif-title:not([class*=" gh-font-heading"]):not([class^="gh-font-heading"]) .kg-toggle-card .kg-toggle-heading-text {
    font-family: var(--gh-font-heading, var(--font-serif));
}

.gh-content .kg-callout-card-accent a {
    text-decoration: underline;
}

.kg-blockquote-alt {
    font-family: var(--font-serif);
    color: var(--color-midgrey);
}

.has-sans-body:not([class*=" gh-font-body"]):not([class^="gh-font-body"]) .kg-blockquote-alt {
    font-family: var(--gh-font-body, var(--font-sans));
}

.kg-card.kg-header-card.kg-style-dark {
    background: var(--color-darkgrey);
}

.kg-header-card.kg-style-light h2.kg-header-card-header {
    color: color-mod(var(--color-darkgrey) l(-5%));
}

.has-serif-title:not([class*=" gh-font-heading"]):not([class^="gh-font-heading"]) .kg-header-card h2.kg-header-card-header {
    font-family: var(--gh-font-heading, var(--font-serif));
}


/* Captions */
figcaption {
    padding: 1.5rem 1.5rem 0;
    text-align: center;
    color: rgba(0,0,0,0.5);
    font-size: 1.3rem;
    line-height: 1.4em;
}
figcaption strong {
    color: rgba(0,0,0,0.8);
}
figcaption a {
    text-decoration: underline;
}


/* Highly specific styles for traditional Instagram embeds */
iframe.instagram-media {
    margin-top: 6vmin !important;
    margin-left: auto !important;
    margin-right: auto !important;
    margin-bottom: 0 !important;
}

iframe.instagram-media + script + :not([id]) {
    margin-top: 6vmin;
}


/* Card captions
/* ---------------------------------------------------------- */

.kg-width-full.kg-card-hascaption {
    display: grid;
    grid-template-columns: inherit;
}

.kg-width-wide.kg-card-hascaption img {
    grid-column: wide-start / wide-end;
}
.kg-width-full.kg-card-hascaption img {
    grid-column: 1 / -1;
}

.kg-width-full.kg-card-hascaption figcaption {
    grid-column: main-start / main-end;
}

.article-comments {
    margin: 6vmin 0 0 0;
}

/* -----old------ */

.footnotes-sep {
    margin-bottom: 30px;
}

.footnotes {
    font-size: 1.5rem;
}

.footnotes p {
    margin: 0;
}

.footnote-backref {
    font-size: 1.2rem;
    font-weight: bold;
    text-decoration: none !important;
    box-shadow: none !important;
}

/* Tables */
.gh-content table:not(.gist table) {
    display: block;
    overflow-x: auto;
    max-width: 100%;
    width: 100%;
    border-spacing: 0;
    border-collapse: separate;
    border-radius: 10px;
    font-family: var(--font-sans);
    font-size: 1.5rem;
    vertical-align: top;
    -webkit-overflow-scrolling: touch;
    background: var(--color-coffee-foam, #FDF7EF);
    box-shadow: 0 2px 12px rgba(45, 24, 16, 0.08);
    margin: 2rem 0;
    border: 1px solid rgba(45, 24, 16, 0.1);
}

.gh-content table:not(.gist table) td:first-child {
    background-image: none;
}

.gh-content table:not(.gist table) td:last-child {
    background-image: none;
}

.gh-content table:not(.gist table) th {
    color: var(--color-coffee-dark, #2D1810);
    font-size: 1.3rem;
    font-weight: 600;
    letter-spacing: 0.3px;
    text-align: left;
    text-transform: uppercase;
    background: var(--color-coffee-haze, #F8EDE0);
}

.gh-content table:not(.gist table) th:first-child {
    border-top-left-radius: 9px;
}

.gh-content table:not(.gist table) th:last-child {
    border-top-right-radius: 9px;
}

.gh-content table:not(.gist table) tr:last-child td:first-child {
    border-bottom-left-radius: 9px;
}

.gh-content table:not(.gist table) tr:last-child td:last-child {
    border-bottom-right-radius: 9px;
}

.gh-content table:not(.gist table) tbody tr:nth-child(even) {
    background: rgba(245, 230, 211, 0.4);
}

.gh-content table:not(.gist table) tbody tr:hover {
    background: rgba(212, 165, 116, 0.15);
}

.gh-content table:not(.gist table) th,
.gh-content table:not(.gist table) td {
    padding: 12px 16px;
    border: none;
    border-bottom: 1px solid rgba(45, 24, 16, 0.08);
}

.gh-content table:not(.gist table) tr:last-child td {
    border-bottom: none;
}


/* 6.1. Post Byline
/* ---------------------------------------------------------- */

.article-byline {
    display: flex;
    justify-content: space-between;
    margin: min(24px, 5.6vmin) 0 0;
}

.article-byline-content {
    flex-grow: 1;
    display: flex;
    align-items: center;
}

.article-byline-content .author-list {
    justify-content: flex-start;
    padding: 0 14px 0 0;
}

.article-byline-meta {
    color: var(--color-secondary-text);
    font-size: 1.4rem;
    line-height: 1.2em;
}

.article-byline-meta .author-name {
    margin: 0 0 6px;
    font-size: 1.7rem;
    font-weight: 700;
    letter-spacing: 0;
}

.article-byline-meta .bull {
    display: inline-block;
    margin: 0 2px;
}

.author-avatar {
    display: block;
    overflow: hidden;
    margin: 0 -4px;
    width: min(56px, 13.6vmin);
    height: min(56px, 13.6vmin);
    border: #fff 2px solid;
    border-radius: 50%;
    background-color: var(--color-border);
}

.page-template .article-title {
    margin-bottom: 0;
}

@media (max-width: 767px) {
    .article-byline-content {
        flex-direction: column;
        align-items: flex-start;
        gap: 16px;
    }

    .article-byline-content .author-list {
        padding-right: 12px;
    }
    .article-byline-meta .author-name {
        margin-bottom: 4px;
    }
}


/* 6.2. Subscribe
/* ---------------------------------------------------------- */

.footer-cta {
    position: relative;
    text-align: center;
}

.footer-cta-title {
    margin: 0 0 min(24px, 6.4vmin);
    font-size: clamp(2.6rem, 5vw, 3.8rem);
    font-weight: 800;
}

.has-serif-title:not([class*=" gh-font-heading"]):not([class^="gh-font-heading"]) .footer-cta-title {
    font-family: var(--gh-font-heading, var(--font-serif));
}

.footer-cta-button {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 500px;
    padding: 5px 5px 5px 15px;
    font-size: 1.7rem;
    color: var(--color-secondary-text);
    background: #fff;
    border: 1px solid var(--color-border);
    border-radius: 8px;
    transition: border-color 0.2s;
}

.footer-cta-button:hover {
    border-color: color-mod(var(--color-border) l(-12%));
}

.footer-cta-button span {
    display: inline-block;
    padding: 9px 15px;
    color: #fff;
    font-size: 1.6rem;
    font-weight: 600;
    letter-spacing: -0.005em;
    background: var(--ghost-accent-color);
    border-radius: 6px;
}


/* 6.3. Read more
/* ---------------------------------------------------------- */

.read-more-wrap {
    margin-top: 2.4vmin;
}

.footer-cta + .read-more-wrap {
    margin-top: max(12vmin, 72px);
}

.read-more {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 4vmin;
}

.read-more .post-card-tags {
    display: none;
}

@media (max-width: 1000px) {
    .read-more {
        grid-template-columns: repeat(4, 1fr);
    }
    .read-more .post-card:nth-child(3) {
        display: none;
    }
}

@media (max-width: 700px) {
    .read-more {
        grid-template-columns: repeat(2, 1fr);
    }
    .read-more .post-card:nth-child(2) {
        display: none;
    }
}

/* 6.4. Comments
/* ---------------------------------------------------------- */
.comments {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 60px 0 44px;
}


.comments-head {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    margin-bottom: 32px;
    width: 100%;
    max-width: 720px;
}

.comments h2 {
    width: 100%;
    max-width: 720px;
    font-weight: 800;
    font-size: 3.4rem;
}

.comments .comment-count {
    color: var(--color-midgrey);
    font-weight: 600;
    white-space: nowrap;
}

.comments #ghost-comments-root {
    width: 100%;
    max-width: 720px;
}


/* 7. Author Template
/* ---------------------------------------------------------- */

.author-profile-pic {
    display: block;
    width: 80px;
    height: 80px;
    object-fit: cover;
    margin: 0 0 2rem;
    background: color-mod(var(--color-lightgrey) l(+10%));
    border-radius: 50%;
}

.author-profile-footer {
    margin-top: 16px;
}

.author-profile-location {
    font-weight: 700;
}

.author-profile-meta {
    display: flex;
    gap: 12px;
}

.author-profile-social-link {
    font-size: 1.3rem;
    color: var(--color-secondary-text);
}

.author-profile-social-link:hover {
    color: var(--color-darkgrey);
}

.author-profile-social-link svg {
    width: 16px;
    height: 16px;
}

@media (min-width: 1001px) {
    .author-template .post-card-large .post-card-content:only-child {
        grid-column: span 2;
        max-width: 640px;
    }
}


/* 8. Tag Template
/* ---------------------------------------------------------- */

.tag-template .post-card-large .post-card-image-link {
    grid-column: 2 / span 2;
    order: 2;
}

.tag-template .post-card-large .post-card-content {
    order: 1;
}

@media (min-width: 1001px) {
    .tag-template .post-card-large .post-card-content:only-child {
        grid-column: span 2;
        max-width: 640px;
    }
}

/* 9. Error Template
/* ---------------------------------------------------------- */

.error-content {
    padding: 14vw 4vw 2vw;
}

.error-message {
    padding-bottom: 10vw;
    text-align: center;
}

.error-code {
    margin: 0;
    font-size: 12vw;
    line-height: 1em;
    letter-spacing: -5px;
}

.error-description {
    margin: 0;
    color: var(--color-secondary-text);
    font-size: 3.2rem;
    line-height: 1.3em;
    letter-spacing: -0.005em;
    font-weight: 400;
}

.error-link {
    display: inline-block;
    margin-top: 5px;
}

@media (min-width: 940px) {
    .error-content .post-card {
        margin-bottom: 0;
        padding-bottom: 0;
        border-bottom: none;
    }
}

@media (max-width: 800px) {
    .error-content {
        padding-top: 24vw;
    }
    .error-code {
        font-size: 11.2rem;
    }
    .error-message {
        padding-bottom: 16vw;
    }
    .error-description {
        margin: 5px 0 0 0;
        font-size: 1.8rem;
    }
}

@media (max-width: 500px) {
    .error-content {
        padding-top: 28vw;
    }
    .error-message {
        padding-bottom: 14vw;
    }
}


/* 10. Site Footer
/* ---------------------------------------------------------- */

.site-footer {
    position: relative;
    margin: max(12vmin, 64px) 0 0 0;
    padding-top: 48px;
    padding-bottom: 140px;
    color: #fff;
    background: linear-gradient(135deg, var(--color-coffee-darker) 0%, var(--color-coffee-dark) 60%, var(--color-coffee-medium) 100%);
}

.site-footer::before {
    content: "";
    position: absolute;
    inset: 0;
    background:
        radial-gradient(600px 280px at 10% 20%, rgba(255, 255, 255, 0.08), transparent 65%),
        radial-gradient(700px 320px at 90% 0%, rgba(255, 255, 255, 0.05), transparent 70%);
    pointer-events: none;
    z-index: 0;
}

.page-template .site-footer {
    margin-top: 0;
}

.site-footer .inner {
    position: relative;
    z-index: 1;
    display: grid;
    grid-gap: 40px;
    grid-template-columns: auto 1fr auto;
    color: rgba(255,255,255,0.7);
    font-size: 1.3rem;
}

.site-footer .copyright a {
    color: #fff;
    letter-spacing: -0.015em;
    font-weight: 500;
}

.site-footer a {
    color: rgba(255,255,255,0.7);
}

.site-footer a:hover {
    color: rgba(255,255,255,1);
    text-decoration: none;
}

.site-footer-nav ul {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 0 20px;
    padding: 0;
    list-style: none;
}

.site-footer-nav li {
    display: inline-flex;
    align-items: center;
    padding: 0;
    margin: 0;
    line-height: 2em;
}

.site-footer-nav a {
    position: relative;
    display: inline-flex;
    align-items: center;
    margin-left: 10px;
}

.site-footer-nav li:not(:first-child) a:before {
    content: "";
    display: block;
    width: 2px;
    height: 2px;
    margin: 0 10px 0 0;
    background: #fff;
    border-radius: 100%;
}

.site-footer-extras {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 32px;
    margin-top: 20px;
}

.site-footer-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

.site-footer-label {
    font-size: 1.1rem;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.5);
}

.site-footer-links {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 14px;
}

.site-footer-links a {
    font-weight: 500;
}

@media (max-width: 767px) {
    .site-footer .inner {
        max-width: 500px;
        grid-template-columns: 1fr;
        grid-gap: 0;
        text-align: center;
    }
    .site-footer .copyright,
    .site-footer .copyright a {
        color: #fff;
        font-size: 1.5rem;
    }
    .site-footer .copyright {
        margin-bottom: 16px;
    }
}


